@import url(https://fonts.googleapis.com/css2?family=Didact+Gothic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
    font-family: 'Didact Gothic', sans-serif;
    color: #707070;
    font-size: 14px;
    letter-spacing: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.Content {
    max-width: 950px;
    padding: 20px;
    box-shadow: 0 0 10px #0000001A;;
    width: 100%;
}

.SideBar {
    margin-top: 20px;
    min-width: 200px;
    width: 250px;
    min-height: calc(100vh - 20px);
    height: 100%;
    background-color: #222629;
    color: #FFFFFF;
}
.NavigationItems {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: center;
    height: 100%;
    flex-flow: column;
    line-height: 50px;
    background-color: #1A1C1F;
}

.NavigationItem {
    line-height: 50px;
    background-color: #222629;
    margin-bottom: 1px;
    position: relative;
}

.NavigationItem a {
    color: #ffffff;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
}


.LinkIcon {
    padding: 0 15px 0 20px;
    display: inline-block;
    font-size: 14px;
    align-items: center;
    border-left: 4px solid transparent;
}


.NavigationItem a.active .LinkIcon {
    color: #39C5C8;
    border-left: 4px solid #39C5C8;
}

.Submenu {
    position: relative;
    left: 0;
    top: 0;
    padding: 0 35px;
    background-color: #1A1C1F;
}

.hideButton {
    position: absolute;
    display: inline-block;
    top: -40px;
    right: 0px;
    color: #39C5C8;
    z-index: 100;
    padding: 20px;
    cursor: pointer;
}

.hideButton:hover {
    transform: rotate(180deg);;
    transition: all 0.1s ease-in;
}

.ContainerFluid {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.Container {
    min-width: 780px;
    display: flex;
    width: 100%;
}

@media screen and (min-width: 1100px) {
    .Container {
        width: 80%;
        max-width: 1200px;
    }
}
table#dashboard {
    background-color: #FFFFFF;
    width: 100%;
    padding: 10px;
    text-align: left;
    border-collapse:separate;
    border-spacing: 0 10px;
}

#dashboard tr{
    line-height: 50px;
    background-color: #EFF0F0;
    position: relative;
}

#dashboard th {
    background-color: #FFFFFF;
    font-size: 14px;
    color: #A09E9E;
    font-weight: 300;
}

#dashboard th:first-of-type {
    padding-left: 20px;
}

#dashboard td:first-of-type {
    padding: 0 20px;
}


#dashboard th svg {
    font-size: 12px;
    padding: 0 10px;
}






.Loader,
.Loader:before,
.Loader:after {
  border-radius: 50%;
}
.Loader {
  color: #00000026;
  font-size: 11px;
  text-indent: -99999em;
  margin: 20px auto;
  position: relative;
  width: 10em;
  height: 10em;
  box-shadow: inset 0 0 0 1em;
  transform: translateZ(0);
}
.Loader:before,
.Loader:after {
  position: absolute;
  content: '';
}
.Loader:before {
  width: 5.2em;
  height: 10.2em;
  background: #EFF0F0;
  border-radius: 10.2em 0 0 10.2em;
  top: -0.1em;
  left: -0.1em;
  transform-origin: 5.2em 5.1em;
  animation: load2 2s infinite ease 1.5s;
}
.Loader:after {
  width: 5.2em;
  height: 10.2em;
  background: #EFF0F0;
  border-radius: 0 10.2em 10.2em 0;
  top: -0.1em;
  left: 5.1em;
  transform-origin: 0px 5.1em;
  animation: load2 2s infinite ease;
}
@keyframes load2 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.DashboardHeader {
    display: flex;
    padding: 5px 15px;
    justify-content: space-between;
    align-items: center;
    background-color: #38D58C;
    color: #FFFFFF;
    font-size: 35px;
    font-family: 'Roboto', sans-serif; /*instead of Helvetica Neue */
    font-weight: 300;
}
